import Vue from 'vue'
import Router from 'vue-router'
import Layout from "@/layouts";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      component: Layout,
      redirect: "editorialBoard",
      children: [
        {
          path: "editorialBoard",
          name: "EditorialBoard",
          component: () => import("@/views/editorialBoard/index")
        },
        
      ],
    },
    {
      path: "/",
      component: Layout,
      children: [
        {
          path: "home",
          name: "Home",
          component: () => import("@/views/home/index")
        },
        
      ],
    },
    {
      path: "/focusAndScope",
      component: Layout,
      children: [
        {
          path: "/focusAndScope",
          name: "FocusAndScope",
          component: () => import("@/views/focusAndScope/index")
        },
      ],
    },
    {
      path: "/peerReiew",
      component: Layout,
      children: [
        {
          path: "/peerReiew",
          name: "PeerReiew",
          component: () => import("@/views/peerReiew/index")
        },
      ],
    },
    {
      path: "/reiewProcess",
      component: Layout,
      children: [
        {
          path: "/reiewProcess",
          name: "ReiewProcess",
          component: () => import("@/views/reiewProcess/index")
        },
      ],
    },
    {
      path: "/openAccess",
      component: Layout,
      children: [
        {
          path: "/openAccess",
          name: "OpenAccess",
          component: () => import("@/views/openAccess/index")
        },
      ],
    },
    {
      path: "/aboutThePublisher",
      component: Layout,
      children: [
        {
          path: "/aboutThePublisher",
          name: "AboutThePublisher",
          component: () => import("@/views/aboutThePublisher/index")
        },
      ],
    },
    {
      path: "/contactUs",
      component: Layout,
      children: [
        {
          path: "/contactUs",
          name: "ContactUs",
          component: () => import("@/views/contactUs/index")
        },
      ],
    },
    {
      path: "/submissionGuidelines",
      component: Layout,
      children: [
        {
          path: "/submissionGuidelines",
          name: "SubmissionGuidelines",
          component: () => import("@/views/submissionGuidelines/index")
        },
      ],
    },
  ]
})



